* {
    box-sizing: border-box;
  }
  
  body {
    margin-left: auto;
    margin-right: auto;
    max-width: 40em;
    width: 88%;
  }
  
  .title {
    display: block;
  }

  h1 {
    text-align: center;
  }
  
  h2 {
    cursor: pointer;
    display: inline-block;
    margin: 0;
  
    &::before {
      content: "\f078";
      font-weight: 900;
      font-family: "Font Awesome 5 Free";
      margin-right: 10px;
      display: inline-block;
      transition: transform 300ms linear;
    }
  }
  
  .accordion-item {
    border: 2px solid #34495F;
    background: #34495F;
    border-radius: 5px;
    margin-bottom: 20px;
    padding: 20px;
    color: #fff;
    &.open {
      .description {
        display: block;
        font-size: 18px;
        line-height: 1.4;
      }
  
      h2::before {
        transform: rotate(180deg);
      }
    }
  
    .description {
      display: none;
    }
  }